import { Component } from 'react'
import { Link } from 'react-router-dom'
import withRouter from '@/Libs/withRouter'
import LeftNav from '@/Components/LeftNav'
import './index.scss'
import backIcon from '@/Assets/images/news/back.png'

// 
import test3 from '@/Assets/images/test/test3.png'

class InformationCom extends Component {
  constructor(props) {
    super(props)
    this.state = {
      num: [1, 2, 3]
    }
  }
  componentDidMount() {
    this.goBackTop()
  }
  // 
  goBackTop = () => {
    document.body.scrollTop = document.documentElement.scrollTop = 0
  }
  goBack = () => {
    this.props.navigate(-1)
  }
  render() {
    return (
      <div className='w content_w'>
        <LeftNav />
        <div className="w_right">
          <div className='information_detail_w'>
            <div className="detail_left">
              <div onClick={ this.goBack } className="detail_crumbs">
                <img src={ backIcon } alt="go_back" />
                <p> </p>
              </div>
              <div className="detail_content">
                <img src={ test3 } alt="" />
                <h3> id={ this.props.params.id }</h3>
                <p>
                2021-22





12
                </p>
              </div>
            </div>
            <div className="detail_right">
              <ul className='detail_recommend'>
                {
                  this.state.num.map(item => {
                    return (
                      <li key={ item }>
                        <Link to="/">
                          <div className="recommend_img"><img src={ test3 } alt="" /></div>
                          <p className='recommend_tit'>11</p>
                          <p className='recommend_intro'>2021-22</p>
                        </Link>
                      </li>
                    )
                  })
                }
              </ul>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
export default withRouter(InformationCom)