import { Component } from 'react'
import './index.scss'

class Notice extends Component {
  constructor(props) {
    super(props)
    this.state = {
      num: [1,2,3,4,5,6,7]
    }
  }
  render() {
    return (
      <div className='news_notice'>
        <ul>
          {
            this.state.num.map(item => {
              return (
                <li key={ item }>
                  <p></p>
                  <span>22-03-28  5:53</span>
                </li>
              )
            })
          }
        </ul>
      </div>
    )
  }
}

export default Notice