import { Component } from 'react'
import { Link } from 'react-router-dom'

import './index.scss'

// 
import test3 from '@/Assets/images/test/test3.png'

class Infomation extends Component {
  constructor(props) {
    super(props)
    this.state = {
      num: [1, 2, 3, 4, 5, 6]
    }
  }
  render() {
    return (
      <div className='news_information'>
        <ul>
          {
            this.state.num.map(item => {
              return (
                <li key={ item }>
                  <Link to={ `/index/infomationDetail/${item}` } state={{ id: item }}>
                    <div className='info_img'><img src={ test3 } alt="" /></div>
                    <p className='info_tit'>2022</p>
                    <p className='info_intro'></p>
                  </Link>
                </li>
              )
            })
          }
        </ul>
      </div>
    )
  }
}

export default Infomation